.search-room {
    display: flex;
    .search-room__filter {
        margin-top: 30px;
        width: 266px;
        margin-right: 60px;
        *, *:before, *:after {
            -webkit-border-sizing: border-box;
            -moz-border-sizing: border-box;
            box-sizing: border-box;
        }
    }
    .search-room__room-list {
        margin-top: 11px;
        .search-room__room-list-text {
            font-size: 24px;
            font-family: 'Open Sans';
            color: #1F2041;
        }
        .search-room__room-list-pagination {
            padding: 0 290px;
            margin-top: 40px;
            margin-bottom: 75px;
        }
    }
    .search-room-room-list__rooms {
        position: relative;
        display: grid;
        grid-template-rows: 8px 8px 8px;
        grid-template-columns: 12px 12px 12px;
        grid-gap: 270px;
        margin-top: 22px;
        .full-stars:nth-child(2) {
            position: absolute;
            top: 216px;
            left: 22px;
        }
        .full-stars:nth-child(4) {
            position: absolute;
            top: 216px;
            left: 304px;
        }
        .full-stars:nth-child(6) {
            position: absolute;
            top: 216px;
            left: 586px;
        }
        .full-stars:nth-child(8) {
            position: absolute;
            top: 493px;
            left: 22px;
        }
        .full-stars:nth-child(10) {
            position: absolute;
            top: 493px;
            left: 304px;
        }
        .full-stars:nth-child(12) {
            position: absolute;
            top: 493px;
            left: 586px;
        }
        .full-stars:nth-child(14) {
            position: absolute;
            top: 771px;
            left: 22px;
        }
        .full-stars:nth-child(16) {
            position: absolute;
            top: 771px;
            left: 304px;
        }
        .full-stars:nth-child(18) {
            position: absolute;
            top: 771px;
            left: 586px;
        }
        .full-stars:nth-child(20) {
            position: absolute;
            top: 1049px;
            left: 22px;
        }
        .full-stars:nth-child(22) {
            position: absolute;
            top: 1049px;
            left: 304px;
        }
        .full-stars:nth-child(27) {
            position: absolute;
            top: 1049px;
            left: 586px;
        }
    }
}
@media(max-width: 1240px) {
    .search-room {
        .search-room__filter {
            margin-right: 20px;
        }
        .search-room-room-list__rooms {
            grid-template-rows: 8px 8px 8px 8px;
            grid-template-columns: 12px 12px 12px;
            grid-gap: 20vw;
        }
    }
    .number-rate-container {
        width: 225px;
        height: 213px;
        .number-rate__photo-slider {
            width: 225px;
            margin-bottom: 0;
            height: 123px;
            .number-rate__btn-previous, .number-rate__btn-next {
                height: 126px;
            }
        }
        .number-rate__img {
            width: 225px;
        }
        .number-rate__name-price-rating-reviews {
            width: 220px;
            .number-rate__line {
                width: 220px;
            }
        }
    }
    .search-room__room-list-text {
        width: 50vw;
    }
    .search-room__room-list {
        min-width: 550px;
        margin: 0 auto;
        .search-room-room-list__rooms {
            margin-bottom: 250px;
        }
    }
}

